<template>
  <div style="text-align:center">
    <form class="form-style">
      <ul>
        <li style="text-align:left;">
          <label for="imporatant">중요여부</label>
          <select name="important" v-model="Notice.important">
            <option value="중요">중요</option>
            <option value="기타">기타</option>
          </select>

          제목
          <input
            type="text"
            name="field2"
            class="field-style"
            placeholder="제목입력"
            v-model="Notice.title"
          />
          아이디
          <input
            style="display:inline; border:1px solid black; width:50px;"
            v-model="Notice.id"
            readonly
          />
        </li>
        <li style="text-align:left;">
          <label for="category">카테고리</label>
          <select name="category" v-model="Notice.category">
            <option value="운영">운영</option>
            <option value="기타">기타</option>
          </select>
          내용
        </li>
        <li>
          <textarea
            name="field5"
            class="field-style"
            placeholder="내용 입력"
            v-model="Notice.contents"
          ></textarea>
        </li>
        <li>
          <span @click.stop="addNotice">등록하기</span>
        </li>
      </ul>
    </form>
    <router-link to="/notice/list" class="btn btn-concrete"
      >목록으로 돌아가기</router-link
    >
  </div>
</template>
<script>
import Constant from "../../Constant";
export default {
  data() {
    return {
      Notice: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      },
      loginStatus: this.$store.state.loginStatus
    };
  },
  created() {
    this.$store.dispatch(Constant.GET_LOGINSTATUS).then(() => {
      this.loginStatus = this.$store.state.loginStatus;
      this.Notice.id = this.loginStatus.id;
    });
  },
  methods: {
    addNotice() {
      if (this.Notice.contents.trim() != "") {
        this.$store.dispatch(Constant.ADD_Notice, { Notice: this.Notice });
        this.$router.push("/notice");
        this.clear();
      }
    },
    clear() {
      (this.Notice.id = ""),
        (this.Notice.title = ""),
        (this.Notice.regdate = ""),
        (this.Notice.contents = "");
    }
  }
};
</script>

<style scoped>
.selectbox {
  position: relative;
  width: 100px; /* 너비설정 */

  border: 1px solid #999; /* 테두리 설정 */

  z-index: 1;

  /* focus 효과*/
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s,
    -webkit-box-shadow ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

/* focus 효과 */
.selectbox.focus {
  border-color: #66afe9;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(102, 175, 233, 0.6);
}

.btn.btn-concrete {
  margin: 10px auto;
  background-color: #7e8c8d;
  border: 5px solid #7e8c8d;
  color: white;
  -webkit-box-shadow: 0 3px 0 #4e5b5c;
  box-shadow: 0 3px 0 #4e5b5c;
  text-decoration: none;
}
.btn.btn-concrete:hover {
  margin: 10px auto;
  border: 5px solid #6a7879;
  background-color: #6a7879;
  color: orangered;
  text-decoration: none;
}
.btn.btn-concrete:active {
  margin: 10px auto;
  top: 3px;
  outline: none;
  color: white;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-decoration: none;
}

.form-style {
  max-width: 450px;
  background: #fafafa;
  padding: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.35);
  border-radius: 10px;
  border: 6px solid #0cb34c;
}
.form-style ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.form-style ul li {
  display: block;
  margin-bottom: 10px;
  min-height: 35px;
}
.form-style ul li .field-style {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 8px;
  outline: none;
  border: 1px solid #3eb994;
}
.form-style ul li .field-style:focus {
  box-shadow: 0 0 5px #3eb994;
  border: 1px solid #3eb994;
}
.form-style ul li .field-split {
  width: 49%;
}
.form-style ul li .field-full {
  width: 60%;
}
.form-style ul li input.align-left {
  float: left;
}
.form-style ul li input.align-right {
  float: right;
}
.form-style ul li textarea {
  width: 100%;
  height: 100px;
}
.form-style ul li input[type="button"],
.form-style ul li input[type="submit"],
.form-style ul li span {
  -moz-box-shadow: inset 0px 1px 0px 0px #18be4f;
  -webkit-box-shadow: inset 0px 1px 0px 0px #00c94d;
  box-shadow: inset 0px 1px 0px 0px #00ce6e;
  background-color: #00a558;
  border: 1px solid #00993b;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  padding: 8px 18px;
  text-decoration: none;
  font: 12px Arial, Helvetica, sans-serif;
}
.form-style ul li input[type="button"]:hover,
.form-style ul li input[type="submit"]:hover,
.form-style ul li span:hover {
  background: linear-gradient(to bottom, #7dff88 5%, #95eeb3 100%);
  background-color: #deffce;
}
</style>
